:root {
  --theme-color: rgb(75, 255, 171);
  --size-4: 0.25rem;
  --size-8: 0.5rem;
  --size-12: 0.75rem;
  --size-16: 1rem;
  --size-20: 1.25rem;
  --size-24: 1.5rem;
  --size-28: 1.75rem;
  --size-32: 2rem;
  --size-36: 2.25rem;
  --size-40: 2.5rem;
  --size-42: 2.75rem;
  --size-48: 3rem;
  --size-56: 3.5rem;
  --size-64: 4rem;
  --theme-font: "Source Code Pro", monospace;
}

.App {
  text-align: center;
  font-family: var(--theme-font);
}

.App-logo {
  height: 16vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 2s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: rgb(75, 255, 171);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1 {
  margin: 0;
  font-size: 1.5rem;
  margin-bottom: 2rem;
}

main {
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.pageHeader {
  font-size: 2.5rem;
  margin: 1.5rem auto;
}

a {
  text-decoration: none;
}

button {
  font-family: var(--theme-font);
}

button:hover {
  cursor: pointer;
}

input::placeholder {
  font-family: var(--theme-font);
}

