#homepageContainer form {
  display: flex;
  flex-direction: column;
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.1),
    rgba(255, 255, 255, 0)
  );
  padding: 2rem;
  backdrop-filter: blur(20px);
  box-shadow: 0 var(--size-8) var(--size-32) 0 rgba(0, 0, 0, 0.37);
  border: 1px solid rgba(255, 255, 255, 0.18);
  border-radius: var(--size-32);
  margin-top: var(--size-12);
  width: 43vw;
  max-width: 43vw;
}

label {
  text-align: left;
  margin: var(--size-4) 3rem var(--size-4) 0;
  position: relative;
}

label header {
  margin: 0;
  font-size: var(--size-24);
}

label header span {
  font-size: var(--size-12);
}

label p {
  font-size: var(--size-16);
  margin: 0;
}

input {
  width: 100%;
  max-width: 100%;
  padding: var(--size-4);
  background: black;
  border: 1px solid var(--theme-color);
  color: white;
  font-size: var(--size-20);
  font-family: var(--theme-font);
}

input:disabled {
  background-color: black;
  color: rgb(73, 72, 72);
}

input:disabled:hover {
  cursor: not-allowed;
}

select {
  font-family: var(--theme-font);
  background: black;
  color: var(--theme-color);
  padding: 7px;
  border: 1px solid var(--theme-color);
}

select:hover {
  cursor: pointer;
}

form button,
form a {
  border: none;
  display: inline-flex;
  justify-content: center;
  width: 75%;
  background: var(--theme-color);
  padding: var(--size-12);
  font-size: var(--size-20);
}

a,
form a {
  background: none;
  padding: 0;
  color: white;
  text-decoration: underline;
  font-size: var(--size-12);
  background: none;
  color: var(--theme-color);
}

button:hover,
a:hover {
  cursor: pointer;
  opacity: 0.5;
}

.checkoutPageContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.progressBar {
  display: flex;
  align-items: center;
  width: 80vw;
}

.progressBarElement {
  display: contents;
}

.progressBar i {
  color: white;
  border: 2px solid var(--theme-color);
  padding: var(--size-24);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  font-size: var(--size-24);
  width: var(--size-48);
  height: var(--size-48);
  border-radius: 50%;
  text-align: center;
  line-height: 100px;
  vertical-align: middle;
}

.progressBar i.completed {
  background-color: var(--theme-color);
  color: black;
}

.progressBarElement p {
  margin: 8px 0 0 0;
  font-size: 0.9rem;
}

.progressBarConnector {
  height: var(--size-12);
  width: 26%;
  background-color: transparent;
  border-top: 2px solid var(--theme-color);
  border-bottom: 2px solid var(--theme-color);
  margin: -22px -2px 0 -2px;
}

.progressBarConnector.completed {
  background-color: var(--theme-color);
}

.checkoutPageMainItems {
  display: flex;
  /* padding: 0 2rem; */
  width: 95vw;
  justify-content: space-evenly;
}

.checkoutPageMainItems form {
  display: flex;
  flex-direction: column;
 /*  align-items: flex-start; */
}

.cartSummary {
  text-align: right;
}

.cartSummaryHeaders header {
  color: var(--theme-color);
  font-size: 1.5rem;
}

.cartSummaryHeaders p {
  margin: var(--size-12) 0;
  font-size: var(--size-20);
}

.cartSummaryItem {
  border-top: 2px solid white;
  border-top: 2px solid var(--theme-color);
  text-align: right;
  margin: var(--size-12) auto;
}

.cartSummaryItem header {
  color: var(--theme-color);
  font-size: 20px;
  margin: 1rem 0;
}

.cartSummaryItem p {
  font-size: var(--size-16);
  color: var(--theme-color);
}

.cartSummaryItem p span {
  color: white;
}

.checkoutBackNextBtnContainer {
  width: 100%;
  max-width: 100%;
  display: flex;
  justify-content: flex-start;
}

.checkoutBackNextBtnContainer button {
  background-color: var(--theme-color);
  border: 1px solid var(--theme-color);
  padding: var(--size-8);
  width: unset;
  margin: var(--size-24) var(--size-12) 0 0;
  font-size: var(--size-20);
}

.checkoutBackNextBtnContainer button:first-child {
  background-color: transparent;
  color: white;
  border: 1px solid white;
}

.itemInCartSummary {
  display: flex;
  margin: 0;
  justify-content: space-between;
  align-items: center;
  border-top: 2px solid var(--theme-color);
  padding: var(--size-8) 0;
}

.itemInCartSummary img {
  height: 110px;
  margin-right: var(--size-12);
}

.itemInCartSummary p {
  font-size: 0.7rem;
  color: var(--theme-color);
}

.itemInCartSummary p span {
  color: white;
}

.itemDetailsShippingPage p:first-child {
  color: var(--theme-color);
  font-weight: 700;
}

.itemDetailsShippingPage p {
  margin: 0.5rem 0;
  font-size: var(--size-12);
}

.itemInfoHeader {
  color: var(--theme-color);
  text-transform: uppercase;
}

/* MOBILE STYLING */
@media screen and (max-width: 962px) {
  h1 {
    margin-bottom: 0rem;
  }

  .progressBar {
    display: flex;
    align-items: center;
    width: 80vw;
    margin: 1.5rem auto;
  }

  .pageHeader {
    margin: 1.5rem auto;
  }

  #homepageContainer form {
    width: 68vw;
    max-width: 68vw;
  }

  label header {
    font-size: var(--size-20);
}
}

@media screen and (max-width: 810px) {
  #homepageContainer form {
    width: 68vw;
    max-width: 68vw;
  }

  .cartSummary {
    text-align: right;
    margin-left: 3rem;
  }
}

@media screen and (max-width: 800px) and (min-height: 1280px) {
  #homepageContainer form {
    margin-top: 2rem;
  }
  label {
    margin: 1rem;
  }
}

@media screen and (max-width: 768px) {
  .progressBar {
    width: 90vw;
  }

  .itemInCartSummary {
    width: 14rem;
}
}

@media screen and (max-width: 601px) {
  .App-logo {
    height: 24vmin;
  }

  .progressBar {
    width: 95vw;
  }

  .progressBar i {
    padding: var(--size-20);
  }

  .progressBarElement p {
    font-size: 0.8rem;
  }

  #homepageContainer form {
    margin: 12px 42px 42px 42px;
  }

  .checkoutPageMainItems {
    flex-direction: column;
  }
  
  .checkoutBackNextBtnContainer {
    justify-content: center;
  }

  .cartSummaryHeaders {
    text-align: center;
    margin-top: 2rem;
  }

  .itemInCartSummary {
    width: 30rem;
  }

  .cartSummaryItem {
    text-align: center;
}
}

@media screen and (max-width: 414px) {
  .App-logo {
    height: 30vmin;
  }

  label {
    margin: 0.5rem 0;
    width: 93%;
  }

  form button, form a {
    width: 86%;
  }

  .progressBar {
    width: 91vw;
  }

  .progressBar i {
      padding: var(--size-8);
  }

  .progressBarElement p {
      font-size: 0.6rem;
  }

  #moreAddressDetails {
    flex-direction: column;
  }

  .cartSummary {
    text-align: right;
    margin-left: 0;
  }

  .itemInCartSummary {
    width: 23rem;
  }
}

@media screen and (max-width: 390px) {
  .itemInCartSummary {
    width: 21rem;
  }
}

@media screen and (max-width: 360px) {
  .checkoutPageMainItems {
    width: 88vw;
  }
  
  #loginBtnsContainer p {
    margin: 0.75rem;
  }

  .progressBarConnector {
    height: 0.5rem;
  }

  .progressBarElement p {
    font-size: 0.5rem;
  }

  .checkoutBackNextBtnContainer button {
    font-size: 1rem;
  }

  .itemInCartSummary {
    width: 18rem;
  }

  .progressBar i {
    font-size: 1rem;
    padding: 0;
  }
}